var Sty_index;
var Sty_Title=[];//标题数组
var Sty_Content=[];//正文数组

export default {
	data() {
		return {
			
		}
	},
	methods: {
		det(){
			uni.navigateTo({
				url:'../det/det'
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor.3"
			})
		}
	},
	onLoad() {
		Sty_Title=[];//标题数组
		Sty_Content=[];//正文数组
		uni.getStorage({
			key:"PJ_ID",
			success: (res) => {
				Sty_index = res.data;
				console.log(Sty_index);
				for (var i = 1;i <= Sty_index ;i++) {
					uni.getStorage({
						key:i + "PS_Title",
						success: (res) => {
							Sty_Title.push(res.data);
							console.log(Sty_Title);
							}
						})
					uni.getStorage({
						key:i + "PS_Content",
						success: (res1) => {
							Sty_Content.push(res1.data);
							console.log(Sty_Content)
						}
					})
				}
			}
		})
	}
}
